import { Component, EventEmitter, Input, Output } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ProvideExpensesService } from '../../provide-expense-service/provide-expenses.service';
import { TableColumn, TableConfig } from '../../../../../../shared/components/table-advanced/table-advanced-interfaces';
import { SharedService } from '../../../../../../services/shared.service';
import { PettyCashService } from '../../../../cashier/petty-cash/services/petty-cash.service';

@Component({
  selector: 'app-provide-expenses-modal',
  templateUrl: './provide-expenses-modal.component.html',
  styleUrls: ['./provide-expenses-modal.component.scss'],
})
export class ProvideExpensesModalComponent {
  @Input() registerDetails: any;
  requestId = 0;
  message: string;
  messageError: string;
  isLoading: boolean = false;
  approvedRequestList = [];
  //Data Table
  tableColumns: TableColumn[] = [];
  loading = false;
  pageNo = 1;
  total = 0;
  limit = 3;
  tableConfig: TableConfig = {
    paging: true,
    filter: {
      Sort: 1,
      PageSize: this.limit,
    },
    hideTotalRecord: true,
  };
  @Output() sendtoLoadData = new EventEmitter();

  constructor(
    private _provideExpensesService: ProvideExpensesService,
    private pettyCashService: PettyCashService,
    private sharedService: SharedService,
    private modalService: NgbModal
  ) { }

  ngOnInit() {
    this.requestId = this.registerDetails?.pettyCashRequestId;
    this.initTableColumns();
    this.getPettyCashRequestExpensesList();
  }
  getPettyCashRequestExpensesList() {
    this.loading = true;
    let params = `?RegisterId=${this.sharedService.getRegister?.registerObj?.id}&ReplenishmentRequestID=${this.requestId}&pageNo=${this.pageNo - 1}&pageSize=${this.tableConfig.filter.PageSize}`
    this.pettyCashService.getPettyCashRequestExpensesList(params).subscribe((response: any) => {
      if (response?.isSuccess) {
        this.approvedRequestList = response.data.data.filter(x => x.status == 2004);
        this.total = response.data?.totalRecordCount;
      }
    }).add(() => this.loading = false);
  }
  provideExpenses() {
    this.isLoading = true;
    const formData = new FormData();
    formData.append('ExpenseRecordId', this.requestId.toString());
    try {
      this._provideExpensesService.provideExpenses(formData).subscribe((response) => {
        if (response.isSuccess == true) {
          this.message = 'Petty cash provided successfully ';
          this.isLoading = false;
          setTimeout(() => {
            this.modalService.dismissAll();
            this.sendtoLoadData.emit();
          }, 3000);
        } else {
          console.log('error', response);
          this.messageError = response.Errors[0]?.ErrorMessageEn;
          this.isLoading = false;
          setTimeout(() => {
            this.messageError = '';
            // this.modalService.dismissAll();
          }, 3000);
        }
      }, error => {
        this.messageError = error?.error.errors[0]?.ErrorMessageEn || error?.error.Errors[0]?.ErrorMessageEn;
        this.isLoading = false;
      });
    } catch (error) {
      console.log(error);
      this.isLoading = false;
    }
  }
  dismissModal() {
    this.modalService.dismissAll();
  }
  onPageChange(page: number) {
    this.pageNo = page;
    this.getPettyCashRequestExpensesList();
  }
  initTableColumns() {
    this.tableColumns = [
      { key: 'invoiceNo', label: 'Invoice No' },
      { key: 'invoiceDate', label: 'Invoice Date', },
      { key: 'category[0].lookupName', label: 'Category', },
      { key: 'costCenter.costCenterName', label: 'Cost Center Name', },
      { key: 'costCenter.approverUser.fullName', label: 'Cost Center Approver' }, 
      { key: 'managerApproval.approvername', label: 'Approved By', placeholder: '---' },
      { key: 'expenseTotalAmount', label: 'Amount', },
      { key: 'createdAt', label: 'Date', },
    ];
  }
}
